* {
  box-sizing: border-box;
}

body {
  font-family: Montserrat, sans-serif;
  line-height: 1.3;
  background-color: #fefefe;
  padding-top: 80px;
}

a {
  text-decoration: none;
  color: #111;
}

.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 50;
  user-select: none;
  border-bottom: 1px solid rgba(17, 17, 17, 0.2);
}

.header:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fefefe;
  z-index: 13;
}

.header__container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px 0 20px;
}

.header__body {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 80px;
  align-items: center;
}

.header__logo {
  flex: 0 0 130px;
  position: relative;
  z-index: 14;
}

.header__logo img {
  display: block;
  max-width: 100%;
}

.header__burger {
  display: none;
}

.header__list {
  position: relative;
  z-index: 13;
  height: 80px;
  display: flex;
  align-items: center;
}

.header__list > li {
  height: 80px;
  margin: 0 0 0 14px;
  list-style: none;
  display: flex;
  align-items: center;
  position: relative;
  cursor: pointer;
}

.header__link {
  line-height: 80px;
  color: #111;
  text-transform: uppercase;
  font-weight: 500;
  text-decoration: none;
}

.header__icons {
  display: flex;
  justify-content: center;
}

.header__icon {
  box-sizing: border-box;
  height: 80px;
  padding: 25px 0;
  margin: 0 0 0 14px;
}

.header .hoverable {
  border-top: 2px solid transparent;
  border-bottom: 2px solid transparent;
}

.header .hoverable:hover {
  border-top: 2px solid #00868a;
  border-bottom: 2px solid #00868a;
}

.cat-arrow {
  margin-left: 10px;
  height: 7px;
  width: 7px;
  background-color: transparent;
  position: relative;
}

.cat-arrow:after {
  position: absolute;
  top: -3px;
  left: 0;
  height: 100%;
  width: 100%;
  content: "";
  border: solid #111;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

.hoverable .cat-arrow {
  display: none;
  order: 2;
}

.sex-container {
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 79px;
  left: -100%;
  width: 80vw;
  max-width: 320px;
  background-color: #fefefe;
  margin: 0;
  padding: 0;
  align-items: stretch;
  box-shadow: 4px 4px 16px rgb(50, 50, 50);
  display: none;
}

.sex-container.show-item {
  display: flex;
}

.sex-element {
  display: flex;
  position: relative;
  margin: 0;
  padding: 0;
  width: 100%;
  border: 0.5px solid rgba(17, 17, 17, 0.2);
}

.sex-header {
  height: 100%;
  margin: 0;
  width: 100%;
  padding: 15px;
  font-weight: 500;
  line-height: 1.3;
  text-transform: uppercase;
}

.season-container {
  display: none;
  position: absolute;
  top: 0;
  left: 320px;
  box-shadow: 4px 4px 16px rgb(50, 50, 50);
  background-color: #fefefe;
}

.season-container.show-item {
  display: flex;
  flex-direction: column;
}

.season-element {
  display: flex;
  border: 0.5px solid rgba(17, 17, 17, 0.2);
}

.season-header {
  width: 160px;
  padding: 15px;
  font-weight: 500;
  line-height: 1.3;
}

.main {
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  background-image: url("../img/main/bg-image.svg"), linear-gradient(90deg, #4fbfa5, #00868a);
  background-repeat: no-repeat;
  background-size: contain;
  position: relative;
}

.main-fix {
  position: absolute;
  bottom: 0;
  height: 80px;
  z-index: -20;
}

.main-container {
  padding: 30px 20px;
  width: 1200px;
  min-height: 500px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-descr {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  color: #fefefe;
  justify-content: space-evenly;
  align-items: flex-start;
  gap: 20px;
}

.main-descr__title {
  font-size: 40px;
  font-weight: 700;
  line-height: 1.3;
  text-shadow: 4px 4px 16px rgb(50, 50, 50);
}

.main-descr__list {
  background-color: rgba(0, 134, 138, 0.6);
  padding: 15px 30px;
  list-style-type: "✓ ";
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  gap: 15px;
  box-shadow: 4px 4px 16px rgb(50, 50, 50);
}

.main-descr__item {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.3;
}

.main-descr__btn {
  padding: 10px 30px;
  border-radius: 20px;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 18px;
  line-height: 1.3;
  letter-spacing: 1px;
  box-shadow: 4px 4px 16px rgb(50, 50, 50);
  cursor: pointer;
  background-color: #fefefe;
  border: 1px solid #00868a;
  color: #00868a;
  text-shadow: 0 0 0px #111;
}

.main .hoverable:hover {
  background-color: transparent;
  background: linear-gradient(to bottom, rgb(0, 161, 166), rgb(0, 107, 110));
  box-shadow: 5px 5px 10px #000;
  color: #fefefe;
}

.main-foto__img {
  margin: 40px 0;
  width: 600px;
  box-shadow: 4px 4px 16px rgb(50, 50, 50);
  border-radius: 20px;
  overflow: hidden;
}

.guarantee {
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  background-color: #e6f3f4;
  position: relative;
}

.guarantee-fix {
  position: absolute;
  bottom: 0;
  height: 80px;
  z-index: -20;
}

.guarantee-container {
  padding: 30px 20px;
  width: 1200px;
  display: flex;
  justify-content: space-between;
  color: #111;
}

.guarantee-repare {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  width: 670px;
}

.guarantee .g__title {
  background-color: #00868a;
  padding: 10px 20px;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 1px;
  line-height: 1.3;
  border-radius: 10px;
  color: #fefefe;
  align-self: flex-start;
  margin-bottom: 20px;
}

.guarantee .repare-summary {
  background-color: #aaded4;
  padding: 20px;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  margin-bottom: 20px;
}

.guarantee .repare-summary > :first-child {
  margin-bottom: 20px;
}

.guarantee .g-summary__text {
  font-size: 18px;
  line-height: 1.22;
  font-weight: 500;
}

.guarantee .g-summary-bold {
  font-weight: 600;
}

.guarantee .g__subtitle {
  padding-left: 10px;
  padding-right: 20px;
  font-size: 16px;
  line-height: 1.2;
  font-weight: 700;
  font-style: italic;
  margin-bottom: 20px;
}

.guarantee .g-condition-decline-container {
  border-radius: 10px;
  border: 4px solid var(--color-corp);
  background-color: #fefefe;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 20px;
}

.guarantee .g-condition-container {
  padding: 15px 20px;
  display: flex;
  flex-direction: column;
}

.guarantee .g-condition-decline__title {
  color: #00868a;
  /* text-shadow: var(--shadow-guarantee); */
  font-weight: 800;
  letter-spacing: -0.5%;
  margin-bottom: 10px;
}

.guarantee .g-condition-decline-list {
  list-style-type: disc;
  padding-left: 20px;
  display: flex;
  flex-direction: column;
}

.guarantee .g-condition-decline__item {
  font-size: 18px;
  font-weight: 500;
  line-height: 1.3;
  margin-bottom: 10px;
}

.guarantee .g-decline-container {
  padding: 15px 20px;
  display: flex;
  flex-direction: column;
}

.guarantee .g-repare-foto {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.guarantee .g-repare-foto__img {
  width: 670px;
  z-index: 0;
  border-radius: 10px;
}

.guarantee .g-repare-foto__text {
  text-align: center;
  position: absolute;
  bottom: 10px;
  z-index: 1;
  color: #fefefe;
  text-shadow: 1px 1px 1px rgb(100, 100, 100);
  font-size: 12.5px;
  line-height: 15px;
  letter-spacing: 1px;
  font-weight: 700;
  font-style: italic;
}

.guarantee-service {
  width: 470px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.guarantee .g-service-foto {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 20px;
}

.guarantee .g-service-foto__text {
  background-color: #00868a;
  color: #fefefe;
  text-align: center;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.5;
  padding: 10px 50px;
}

.guarantee .g-service-price {
  background-color: #aaded4;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
}

.guarantee .g-service-price__title {
  color: #008c94;
  align-self: center;
  font-weight: 800;
  font-size: 18px;
  line-height: 1.5;
  margin: 10px;
}

.guarantee .g-service-price__list {
  list-style-type: disc;
  padding-left: 30px;
  margin-bottom: 20px;
}

.guarantee .g-service-price__item {
  font-weight: 500;
  font-size: 18px;
  line-height: 1.5;
}

.guarantee .g-service-price.gs-500__block {
  margin-bottom: 20px;
}

.shops {
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
  position: relative;
  z-index: -10;
}

.shops-fix {
  position: absolute;
  bottom: 0;
  height: 80px;
  z-index: -20;
}

.shops-container {
  padding: 20px;
  width: 1200px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.shops__title {
  color: #00868a;
  font-size: 40px;
  line-height: 1.5;
  font-weight: 700;
  text-align: center;
  margin-bottom: 20px;
}

.shops-block {
  display: flex;
  justify-content: space-between;
  color: #fefefe;
}

.shops-item {
  position: relative;
  width: 275px;
  height: 350px;
  border-radius: 10px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 20px 10px 20px;
}

.shops-item__title {
  font-size: 30px;
  line-height: 1.5;
  letter-spacing: 1.5px;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 30px;
}

.shops-item__subtitle {
  display: none;
  margin-bottom: 10px;
  font-size: 10px;
  line-height: 1.5;
  font-weight: 500;
  font-style: italic;
  height: 20px;
  vertical-align: top;
}

.shops-item__address {
  font-weight: 700;
  font-size: 16px;
  line-height: 1.5;
  margin-bottom: 50px;
  text-align: center;
}

.shops-item__schedule {
  text-align: center;
  margin-bottom: 45px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.5;
  position: relative;
  font-style: italic;
}

.shops-item__schedule:before,
.shops-item__schedule:after {
  content: "";
  background-color: #00868a;
  height: 2px;
  width: 100px;
  position: absolute;
  left: calc(50% - 50px);
}

.shops-item__schedule:before {
  top: -10px;
}

.shops-item__schedule:after {
  bottom: -10px;
}

.shops-item__phone {
  align-self: stretch;
  text-align: center;
  margin: 0 10px 25px;
  padding: 2px auto;
  background-color: #00868a;
  background-color: rgba(0, 134, 138, 0.6);
  color: #fefefe;
  font-size: 17px;
  font-weight: 900;
  line-height: 1.5;
  letter-spacing: 1px;
  border-radius: 20px;
}

.shops-item__phone.hoverable:hover {
  background-color: transparent;
  background: linear-gradient(to bottom, #00868a, rgb(0, 108, 110));
}

.shops-item__img {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
}

#contacts {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color: #fefefe;
  padding-top: 20px;
  user-select: none;
}

.contacts-title {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 40px;
  line-height: 1.5;
  color: #00868a;
}

.contacts-container {
  width: 1200px;
  padding: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-gap: 10px 20px;
}

.contacts-item {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  border-top: 3px solid #00868a;
  padding: 10px 0;
}

.cont-item__title {
  font-weight: 700;
  line-height: 1.5;
  font-size: 20px;
  margin-bottom: 0;
}

.cont-item__subtitle {
  font-weight: 700;
  font-style: italic;
  line-height: 1.5;
  font-size: 15px;
  margin-bottom: 10px;
}

.cont-item__address {
  font-weight: 500;
  font-size: 15px;
  line-height: 1.5;
  margin-bottom: 10px;
  text-decoration: underline;
}

.cont-item__shedule {
  font-weight: 500;
  font-size: 15px;
  line-height: 1.5;
  font-style: italic;
  margin-bottom: 10px;
}

.cont-item-phone__block {
  display: flex;
  height: 45px;
  align-items: center;
}

.phone-img {
  margin-right: 10px;
  height: 25px;
}

.phone-number {
  display: flex;
  flex-direction: column;
  font-weight: 500;
  font-size: 15px;
  line-height: 1.5;
}

#contacts .hoverable:hover {
  text-decoration: underline;
}

.production-tile .cont-item__shedule {
  font-weight: 700;
}

#footer {
  background-color: #00868a;
  display: flex;
  justify-content: center;
  height: 50px;
}

.hover-block {
  display: none;
}

.footer-container {
  padding: 0 20px;
  width: 1200px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.f-text-link {
  color: #fefefe;
  font-weight: 500;
  text-shadow: 4px 4px 16px rgb(50, 50, 50);
}

.footer-container > .hoverable:hover {
  text-decoration: underline;
  transform: scale(1.2);
}

.f-facebook-link,
.f-insta-link {
  box-sizing: border-box;
  padding: 5px;
  position: relative;
  z-index: 0;
}

.show-qr {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  z-index: 1;
  bottom: 50px;
  right: 0;
  border: 2px solid #00868a;
  border-radius: 10px;
  background-color: #fefefe;
  width: 200px;
  height: 200px;
  box-shadow: 4px 4px 16px rgb(50, 50, 50);
}

#spinner {
  display: block;
}

.spinner-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fefefe;
  z-index: 999;
}

.spinner__img {
  height: 200px;
  width: 200px;
}

#catalog {
  background-color: #fefefe;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 20px 0 0 0;
  background-color: #e5e5f7;
  opacity: 1;
  background-size: 10px 10px;
  background-image: repeating-linear-gradient(45deg, #A3D3D4 0, #A3D3D4 1px, #fefefe 0, #fefefe 50%);
}

.catalog-container {
  padding: 10px 20px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 40px;
  max-width: 1200px;
  margin: 10px auto;
  user-select: none;
}

.error-title {
  font-weight: 600;
  display: none;
}

.error-title.show-item {
  display: flex;
  align-items: center;
}

.catalog-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px;
  background-color: #fefefe;
  box-shadow: 4px 4px 16px rgb(50, 50, 50);
  border-radius: 20px;
  position: relative;
  max-width: 400px;
}

.show-item {
  display: flex;
}

.hide-item {
  display: none;
}

.c-item__img {
  background-color: lightgray;
  height: 1fr;
  width: 100%;
}

.c-item__name {
  margin: 5px 0;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.5;
}

.c-item-descr {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  height: 120px;
}

.c-item-descr p {
  margin: 0;
  font-size: 15px;
  font-weight: 500;
  line-height: 20px;
}

.c-item-descr span {
  font-weight: 400;
}

.c-item-spa-block {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.c-item__size {
  display: flex;
  margin: 10px 0;
  justify-content: space-between;
}

.size {
  background-color: #98d6cc;
  border-radius: 10px;
  height: 35px;
  width: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.33;
}

/* price-start */

.c-item-price__block {
  margin: 0 0 10px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.action-disabled {
  display: none;
}

.action-enabled {
  text-align: center;
  color: red;
  font-size: 28px;
  font-weight: 700;
  line-height: 28px;
  margin-right: 20px;
}

.c-item-price__elem {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.img-invisible {
  display: none;
}

.img-visible {
  position: absolute;
  left: 0;
  z-index: 1;
  width: 100%;
  margin: 0;
  padding: 0;
}

.price-active {
  font-size: 22px;
  font-weight: 700;
  color: #111;
  line-height: 28px;
}

.price-inactive {
  color: #777;
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
  position: relative;
  z-index: 0;
}

.price-inactive:before {
  content: "";
  position: absolute;
  background-color: #777;
  height: 3px;
  width: 100%;
  left: 0;
  top: calc(50% - 1.5px);
  z-index: 1;
}

/* price-end */

.c-item__art {
  display: flex;
  align-self: stretch;
  justify-content: flex-end;
  height: 40px;
}

.art__text {
  background-color: #98d6cc;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  font-weight: 600;
  line-height: 22px;
  flex: 1 1 auto;
  border: 2px solid #00868a;
  user-select: text;
}

.art__text span {
  font-weight: 400;
  margin-right: 10px;
  user-select: none;
}

.art__textarea {
  border: none;
  padding: 0;
  margin: 0;
  font-size: inherit;
  font-family: inherit;
  line-height: inherit;
  background-color: transparent;
  resize: none;
  overflow: hidden;
  vertical-align: middle;
  color: inherit;
  font-weight: 600;
  height: 22px;
  width: 100px;
  user-select: none;
  pointer-events: none;
}

.art__button {
  background-color: #00868a;
  padding: 11px;
  cursor: pointer;
}

.art__button:hover {
  background-color: #4fbfa5;
}

.art__img {
  height: 20px;
  width: 20px;
  user-select: none;
}

.copied::before {
  position: absolute;
  font-size: 0.7vw;
  bottom: 0px;
  left: 30px;
  content: "Текст був успішно скопійований в буфер обміну";
