  }

  .header__burger:before,
  .header__burger:after {
    content: "";
    background-color: #00868a;
    position: absolute;
    width: 100%;
    height: 2px;
    border-radius: 1px;
    left: 0;
    transition: all 0.3s ease 0s;
  }

  .header__burger:before {
    top: 0;
  }

  .header__burger::after {
    bottom: 0;
