    justify-content: space-between;
  }

  .guarantee .g-service-price {
    flex: 1 1 40%;
  }

  .guarantee .g-service-price__title {
    font-size: 16px;
  }

  .guarantee .g-service-price__item {
    font-size: 16px;
  }

  .guarantee .g-service-price.gs-500__block {
