  .shops-fix {
    height: 50px;
  }

  .shops-container {
    width: 100vw;
    padding: 15px 5px;
  }

  .shops__title {
    font-size: 24px;
  }

  .shops-block {
    display: grid;
    max-width: 100%;
    height: 120vw;
    grid-template-columns: 5fr 5fr;
    grid-template-rows: 6fr 6fr;
    grid-gap: 5px;
  }

  .shops-item {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 6vw 0;
    justify-content: space-between;
  }

  .shops-item__title {
    margin: 0;
    line-height: 1;
    letter-spacing: 0.5px;
    font-size: 6vw;
  }

  .shops-item__address {
    margin: 0;
    height: 4vw;
    font-size: 3vw;
  }

  .shops-item__address.shop-address-2 {
    padding: 0;
  }

  .shops-item__address.shop-address-3 {
    padding: 0;
  }

  .shops-item__schedule {
    font-size: 3vw;
    margin: 0;
  }

  .shops-item__phone {
    font-size: 4vw;
    margin: 0 5px;
    padding: 0;
  }

  #contacts {
    padding-top: 10px;
  }

  .contacts-container {
    max-width: 100%;
    padding: 5px;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr;
    grid-gap: 5px;
  }

  .contacts-title {
    font-size: 24px;
  }

  .cont-item__title {
    font-size: calc((100vw - 15px) / 23);
    letter-spacing: -0.5px;
  }

  .cont-item__subtitle {
    font-size: calc((100vw - 15px) / 28);
  }

  .cont-item__address {
    font-size: calc((100vw - 15px) / 29);
  }

  .cont-item__shedule {
    font-size: calc((100vw - 15px) / 30);
  }

  .phone-img {
    height: 20px;
    margin-right: 5px;
  }

  .phone-number {
    font-size: calc((100vw - 15px) / 30);
    letter-spacing: -0.5px;
  }

  #footer {
    height: auto;
  }

  .footer-container.f-grid__container {
    width: 100%;
    padding: 5px;
    max-width: 350px;
    display: grid;
    grid-template-areas: "item6 item1 item7" "item6 item2 item7" "item3 item4 item5";
    grid-row-gap: 20px;
  }

  .f-grid__item {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .f-grid__item.item-1 {
    grid-area: item1;
  }

  .f-grid__item.item-2 {
    grid-area: item2;
  }

  .f-grid__item.item-3 {
    grid-area: item3;
  }

  .f-grid__item.item-4 {
    grid-area: item4;
  }

  .f-grid__item.item-5 {
    grid-area: item5;
  }

  .f-grid__item.item-6 {
    border: 2px solid #f0f0f0;
    border-radius: 20px;
    grid-area: item6;
    padding: 10px;
    background-color: rgba(17, 17, 17, 0.1);
  }

  .f-grid__item.item-7 {
    grid-area: item7;
    border: 2px solid #f0f0f0;
    border-radius: 20px;
    padding: 10px;
    background-color: rgba(17, 17, 17, 0.1);
  }

  .catalog-container {
    max-width: 350px;
    grid-gap: 10px;
    grid-template-columns: repeat(auto-fit, minmax(290px, 1fr));
    padding: 0 5px;
  }

  .c-item__img {
    height: auto;
    width: 100%;
  }

  .c-item__size {
    display: flex;
    margin: 10px 0;
    justify-content: space-around;
  }

  .size {
    background-color: #98d6cc;
    border-radius: 10px;
    height: 40px;
    width: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.33;
  }
}